@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  max-width: 1440px;
  height: 100vh;
  padding: 100px 138px;
  font-family: "Open Sans", sans-serif;
}

@media (max-width: 768px) {
  .App {
    padding: 100px 82px;
  }
}
@media (min-width: 320px) and (max-width: 767px) {
  .App {
    padding: 24px 16px;
  }
}

.main{display:flex;justify-content:space-between;flex-wrap:wrap}.main__item{width:23%;min-width:230px;position:relative;margin-bottom:24px;cursor:pointer}.main__item:hover .main__item-info{opacity:1}.main__item::after{content:"";position:absolute;bottom:0;left:0;width:100%;height:50%;background:linear-gradient(0deg, rgba(34,34,34,0.85) 30.64%, rgba(38,38,38,0) 81.78%);border-bottom-right-radius:4px;border-bottom-left-radius:4px}.main__item-info{position:absolute;bottom:0;left:0;color:white;padding:12px 16px;text-align:left;z-index:1;opacity:0.8}.main__item-pic img{width:100%;background-size:cover;background-repeat:no-repeat;display:block;position:relative;border-radius:4px}.main__item-title{font-weight:700;font-size:16px;line-height:24px;margin-bottom:4px}.main__item-description{font-size:12px;line-height:18px}.main__set{font-size:20px;padding:10px}.main__set button{cursor:pointer;font-size:20px;height:30px;display:flex;justify-content:center;align-items:center;background-color:white;border-radius:4px;border:1px solid black;opacity:0.8;outline:none}.main__set button:hover{opacity:1}.main__set button:disabled{cursor:auto;cursor:initial;background-color:rgba(128,128,128,0.4)}.main__set button:disabled:hover{opacity:0.8}.main__set-title{font-weight:700;margin-bottom:10px}.main__set-input{margin-bottom:10px;display:flex}.main__set-input input{margin-right:10px;font-size:20px;height:25px}@media (max-width: 768px){.main__item{width:48%}.main__set{font-size:14px}.main__set button{font-size:12px;height:20px}.main__set-input input{font-size:12px;height:15px}}@media (min-width: 320px) and (max-width: 767px){.main{display:block}.main__item{width:100%;display:flex}.main__item::after{display:none}.main__item-info{position:static;position:initial;color:black;line-height:18px;width:calc(100% - 112px);padding:0}.main__item-pic{width:100px;height:100px;margin-right:12px}.main__item-pic img{object-fit:cover;width:100%;height:100%}.main__item-title{font-size:14px;margin-bottom:4px;line-height:normal;line-height:initial}}

.full{display:flex;flex-direction:column;align-items:center}.full__info-title{font-size:24px;font-weight:700;margin:10px 0}.full__info-description{font-size:20px;margin-bottom:10px}.full__info-views{font-size:24px;color:grey}.arrow{position:absolute;top:10px;left:10px;text-decoration:none;font-size:20px;color:black;opacity:0.8}.arrow:hover{opacity:1}@media (min-width: 320px) and (max-width: 767px){.full{padding-top:20px}.full__info-title{font-size:14px}.full__info-description{font-size:12px;margin-bottom:10px}.full__info-views{font-size:14px;color:grey}.arrow{font-size:12px}}

